import Navbar from "./Navbar";
import Navigation from "./Navigation";
import { useState } from "react";
import VideoCard from "./VideoCard";
import Videos from "./Videos";
// import Videos from "./VideoCard";

export default function Layout() {
  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  const toggleSidebarOpen = () => setIsSidebarOpen((prev) => !prev);

  return (
    <div className="border-1 border-amber-600 md:px-3 h-screen">
      <Navbar toggleSidebarOpen={toggleSidebarOpen} />
      <Navigation isSidebarOpen={isSidebarOpen} />
      <Videos />
    </div>
  );
}
